<template>
  <div id="remotes">
    <el-container>
      <el-header height="30px">
        <div class="command">
          <div>{{ currentData }}</div>
          <!-- <div>
                <el-button>新建</el-button>
                <el-button>上传</el-button>
                <el-button>导入</el-button>
                <el-button>下载</el-button>
                <el-button>显隐</el-button>
                <el-button>标注</el-button>
                <el-button>删除</el-button>
              </div> -->
        </div>
      </el-header>
      <el-container>
        <el-aside width="180px">
          <el-col :span="24">
            <div class="right">
              <ResultList></ResultList>
            </div>
          </el-col>
        </el-aside>
        <el-container>
          <el-main>
            <!-- <div class="pic"> -->
            <el-row>
              <el-col
                :span="6"
                v-for="(src, index) in remoteDate.tifList"
                :key="src + index"
              >
                <div class="col-content">
                  <viewer :images="remoteDate.tifList">
                    <img
                      v-for="(src, index) in remoteDate.tifList"
                      :src="src"
                      :key="src + index"
                    />
                  </viewer>
                </div>
              </el-col>
            </el-row>
          </el-main>
          <el-footer height="30px">
            <div class="footer">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pageSize"
                :layout="layout"
                :total="remoteDate.total_files"
              >
              </el-pagination>
            </div>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import ResultList from "../DetailsComponent/ResultList";
export default {
  components: {
    ResultList,
  },
  data() {
    return {
      currentPage: 1,
      //总条数
      total: null,
      //当前每页条数
      pageSize: 9,
      //控制每页有多少条数
      pageSizes: [9, 12],
      //控制 分页组件的布局顺序
      layout: "total, sizes, prev, pager, next, jumper",
      currentData: "",
      imagsList: [
        {
          id: 0,
          details: "已标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 1,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 2,
          details: "已标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 3,
          details: "已标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 4,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 5,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 6,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 7,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 8,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 9,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 10,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
        {
          id: 11,
          details: "未标注",
          url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
        },
      ],
      remoteDate: {
        tifList: [],
        total_pages: 0,
        total_files: 0,
      },
      loading: "",
    };
  },
  computed: {
    // 二级导航栏
    menulist() {
      return this.$store.state.ItemDetails.menuList;
    },
    // 二级导航栏的选中值
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    // 选中一级类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    itemDate: {
      handler(newValue, oldValue) {
        if (this.currentType == "remote_sensing") {
          console.log(newValue, oldValue);
          this.currentData = newValue;
          console.log(this.currentData);
          // this.getPageData();
          this.getPage();
        }
      },
      deep: true,
    },
  },
  methods: {
    // 分页接口
    getPage() {
      const that = this;

      if (this.$store.state.ItemDetails.currentType === "remote_sensing")
        this.loading = this.$loading({
          lock: true,
          text: "正在加载数据...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
      this.$api
        .getPageData({
          dataSetName: this.currentData,
          page_number: this.currentPage,
          page_size: this.pageSize,
        })

        .then((res) => {
          console.log(res);

          if (res.code === 200) {
            that.remoteDate.tifList = res.tifList;
            that.remoteDate.total_files = parseInt(res.total_files);
          }
          that.loading.close();
        });
    },
    handleSizeChange(size) {
      console.log(`每页 ${size} 条`);
      this.pageSize = size;
      this.getPage();
    },
    handleCurrentChange(page) {
      console.log(`当前页: ${page}`);
      this.currentPage = page;
      this.getPage();
    },
  },
  mounted() {
    // this.reLoad();
  },
  created() {},
};
</script>
<style lang="scss" scope>
#remotes {
  min-width: 1200px;
  .el-container {
    .el-header {
      padding: 0;
      .command {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 30px;
        padding: 0 2vh;
        background-color: #222337;
        color: #fff;
      }
    }
    .el-aside {
      // min-height: 860px;
      // max-height: 860px;
      overflow: hidden;
      height: calc(100vh - 132px);
    }
    .el-main {
      padding: 0;
      height: calc(100vh - 172px);
      // min-height: 745px;
      // max-height: 745px;
      // overflow: auto;
      .el-row {
        // margin: 2px;
        .el-col {
          // margin-right: 0.2rem;
          padding-left: 1rem;
          &:nth-child(3n) {
            // margin-right: 0;
            padding-right: 0;
          }
        }
        .col-content {
          display: flex;
          height: 12rem;
          width: 100%;
          min-height: 36px;
          text-align: center;
          justify-content: center;
          border-radius: 4px;
          margin: 4px 0;

          background: #fff;
        }
      }
    }
    .el-footer {
      padding: 0;
      margin-bottom: 10px;

      .footer {
        display: flex;
        justify-content: center;
        align-items: center;
        // height: 30px;
        background-color: #222337;
      }
    }
  }
}
</style>
